export function validate (value, type) {
  if (!type) return true;
  if (type === 'login') {
    const regexp = /^(?=.*[A-Za-z0-9]$)[A-Za-z][\_A-Za-z\d.-]{3,19}$/;
    if (regexp.test(value)) {
      return { isValid: true };
    }
    return { isValid: false, error: '  4-20 .    , , ,   ' };
  } else if (type === 'password') {
    const regexp = /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)[a-zA-Z\d]{8,14}$/;
    if (regexp.test(value)) {
      return { isValid: true };
    }
    return { isValid: false, error: '  8-14 .         ' };
  } else if (type === 'required') {
    if (value.length) {
      return { isValid: true };
    }
    return { isValid: false, error: ' ' };
  } else if (type === 'phone') {
    const regexp = /^\+7 \([0-9]{3}\) [0-9]{3} - [0-9]{2} - [0-9]{2}$/;
    if (regexp.test(value)) {
      return { isValid: true };
    }
    return { isValid: false, error: '   ' };
  }
  console.error('Unknown validation type');
}